.create,
.design {
  margin: 0;
}
.create {
  color: #9ad7f3;
}
.inspire {
  margin: 0;
  color: #6bdfda;
}
.design-create-inspire-container {
  position: relative;
  font-size: 72px;
  display: inline-block;
  width: 370px;
}
.lorem-ipsum-dolor {
  position: relative;
  color: var(--color-white);
  display: inline-block;
  width: 449px;
}
.contact-us {
  position: relative;
  line-height: 18px;
}
.primary-button,
.text {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.primary-button {
  border-radius: 36px;
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: row;
  padding: 24px 48px;
  color: #232323;
}
.text {
  flex-shrink: 0;
  flex-direction: column;
  gap: 24px;
}
.frame-child {
  position: relative;
  border-radius: var(--br-21xl);
  width: 172px;
  height: 192px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.frame-parent {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.div,
.image-gallery {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.image-gallery {
  flex-shrink: 0;
  justify-content: flex-start;
  gap: 18px;
}
.div {
  position: relative;
  background-color: #2f2f2f;
  width: 100%;
  overflow: hidden;
  justify-content: space-between;
  padding: 67px 64px;
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: #fec3c7;
  font-family: var(--font-poppins);
}
